:host {
  display: block;
  height: 100%;
}

::ng-deep {
  // Target the accordion container
  p-accordion,
  .p-accordion {
    display: flex !important;
    flex-direction: column !important;
    height: 100% !important;
  }

  // Panel Styling
  p-accordion-panel,
  .p-accordionpanel {
    display: flex !important;
    flex-direction: column !important;

    // Default: Shrink to fit header (collapsed)
    flex: 0 0 auto !important;

    // Active/Expanded State
    &.p-accordionpanel-active,
    &.p-highlight {
      // Grow to fill space
      flex: 1 1 auto !important;
      min-height: 0 !important;
      overflow: hidden !important;
    }
  }

  // Header Styling
  p-accordion-header,
  .p-accordionheader {
    flex: 0 0 auto !important;
    z-index: 10;
  }

  // Content Styling
  p-accordion-content,
  .p-accordioncontent {
    // Fill the remaining space in the panel
    flex: 1 1 auto !important;
    // Scroll the content, not the container
    overflow-y: auto !important;
    height: 100% !important; // Ensure it tries to fill via height too

    // Fix for internal wrapper if present
    .p-accordioncontent-content {
      min-height: 100%;
    }
  }
}
